import React from "react"
import {  graphql , Link} from 'gatsby'
import styled from "styled-components"
import posed from "react-pose";
import nav_up from "../images/nav_up.svg"
import nav_close from "../images/nav_close.svg"
import nav_down from "../images/nav_down.svg"
import chain from "../images/chain.svg"
import { DOMParser } from "xmldom"

const LinkIcon = styled.div`

  background-color: #3c3c3c;
  background-image: url(${chain});
  background-repeat: no-repeat;
  background-position: right 20px center;
`

const SideBar = styled.div`
  width: 0;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  background-color: #fefefe;
  position: sticky;
  top: 98px;
`

const SideBarTitle = styled.div`
  color: #3c3c3c;
  font-weight: bold;
  padding: 5px 15px;
`
const SideBarContent = styled.div`
  padding: 5px 0;
  ul {
    list-style: decimal-leading-zero;
    text-align: left;
    margin: 0 auto;
    border-bottom: 1px dotted #5c5c5c;
    padding-left: 30px;
  }
  li {
    height: 25px;
    padding: 0 10px;
    
    a {
      border: 0;
      margin: 0 3px;
    }
    
    &:before {
      content: url(${chain});
      width: 18px;
      height: 18px;
      display: inline-block;
      vertical-align: bottom;
    }
  }
`

const TableOfContent = styled.div`
  width: 100%;
`

const SideBarAnimator = posed(SideBar)({
  enter: {
    x: 0,
    delay: 300,
    opacity: 1,
    transition: { ease: 'easeOut', duration: 700 }
  },
  exit: {
    x: -600,
    opacity: 0,
    transition: { ease: 'easeIn', duration: 700 }
  },
})

const SideBarWrapper = styled.div`
  min-width: 150px;
  width: 10vw;
  margin-left: calc(-11vw - 20px);
`

const PostContainer = styled.div`
  width: 99%;
  display: inline-block;
`

const RightToLeft = posed(PostContainer)({
  enter: {
    x: 0,
    delay: 300,
    opacity: 1,
    transition: { ease: 'easeOut', duration: 700 }
  },
  exit: {
    x: 400,
    opacity: 0,
    transition: { ease: 'easeIn', duration: 700 }
  },
})


const Anchor= styled.div`
  width:0;
  height:0;
`

const TopAnchor = styled(Anchor)`
  position: absolute;
  top: -30px;
`

const PostFrame = styled.div`
  position: relative;
  min-height: 300px;
`

const PostContent = styled.div`
  margin: 0;
  display: inline-block;
  width: 100%;
  p {
   padding: 0 10px;
  }
`

const Navigator = styled.div`
  position: sticky;
  top: 98px;
  width: 0;
  display: inline-block;
  vertical-align: top;
`

const Nav = styled(Link)`
  background-color: #3c3c3c;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  margin-bottom: 5px;
  color: #f1f1f1;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  display: block;
    -webkit-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  border: 1px #999999 solid;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    -moz-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -2px) scale(1.01);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.2s ease-out;
    }
  }
  
`

const NavUp = styled(Nav)`
  background-image: url(${nav_up});
  background-color: #748cc5;
`

const NavClose = styled(Nav)`
  background-image: url(${nav_close});
  background-size: 70%;
  background-color: #cb586a;
`

const NavDown = styled(Nav)`
  background-image: url(${nav_down});
  background-color: #748cc5;
`
const Post = styled.div`
  display: inline-block;
  width: 100%;
`

const PagingFooter = styled.div`
  width: 100%;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  background-color: #f1f1f1;
  border-top: 1px solid #bebebe;
`

class Commento extends React.Component {
  constructor(...args) {
    super(...args)
    this.ref = React.createRef()
  }
  render() {
    return (
      <div>
        <div id="commento"> </div>
        <div ref={this.ref} />
      </div>
    )
  }
  componentDidMount() {
    var async = true;
    const s = document.createElement('script')
    s.async = async;
    s.src = 'https://cdn.commento.io/js/commento.js'
    // s.setAttribute('data-timestamp', +new Date())
    this.ref.current.appendChild(s)
  }
}

const PostWrapper = styled.div`
  width: 800px;
  margin: 0 auto;
`


export default ({ data, pageContext }) => {
  const post = data.markdownRemark

  return (
      <PostWrapper>
          <TopAnchor  id="top-anchor" />
          <PostFrame>
            <SideBarAnimator>
              <SideBarWrapper>
                <TableOfContent>
                  {(() => {
                    const toc = new DOMParser().parseFromString(post.tableOfContents,'text/html')
                    const list = toc.getElementsByTagName("a")

                    let cString = '<ul>';
                    for (let listData of Array.from(list)) {
                      let element = Array.from(listData.childNodes).map(el => el.nodeValue)
                      if (element.length>0) {
                        cString += '<li><a href="'+listData.getAttribute('href')+'">'+element[0]+'</a></li>'
                      }
                    }
                    cString +='</ul>'
                    return <SideBarContent dangerouslySetInnerHTML={{__html: cString}} />
                  })()}


                  <SideBarTitle>Index</SideBarTitle>
                  <SideBarContent dangerouslySetInnerHTML={{__html: post.tableOfContents}} />
                </TableOfContent>
              </SideBarWrapper>
            </SideBarAnimator>
            <RightToLeft >
              <Post>
                <h1>{post.frontmatter.title}</h1>
                <PostContent dangerouslySetInnerHTML={{ __html: post.html }}  />
              </Post>
              <Navigator>
                <NavUp to={post.fields.path+"/#top-anchor"} />
                <NavClose to={"/#"} onClick={(event)=>{window.history.back(); event.preventDefault();}}/>
                <NavDown to={post.fields.path+"/#bottom-anchor"} />
              </Navigator>
            </RightToLeft>
          </PostFrame>
          <Anchor id="bottom-anchor"/>
          <PagingFooter>
          </PagingFooter>
        <Commento/>
      </PostWrapper>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents(pathToSlugField: "fields.path")
      frontmatter {
        title
        url
      }
      fields {
        path
      }
    }
  }
`

if (typeof window !== "undefined") {
  // eslint-disable-next-line global-require
  require("smooth-scroll")('a[href*="#"]',{ speed: 150, easing: 'easeOutQuart', updateURL: false,})
}